* {
    font-family: Microsoft JhengHei;
}

body {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    margin: 0;
    & > main {
        cursor: pointer;
        margin: 10vh auto 0;
        display: flex;
        width: fit-content;
        height: fit-content;
        border-radius: 10em;

        backdrop-filter: blur(10px);
        transform: translateZ(0);//加強性能

        color: rgb(0, 0, 0);
        background-color: rgba(255, 255, 255, 0.6);
        text-shadow: 0 0 0.6em white;
        box-shadow: 0 0 0.6em 3px rgba(120, 120, 120, 0.6);
    }
}

.background {
    z-index: -1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

#title {
    margin: 20vh auto 0;
    padding: 0.8em;
    font-size: unquote("min(max( 2em ,3vw),4em)");
    animation: fade_in 0.85s ease-in-out;
}
#hello_time {
    margin: 10vh auto 0;
    padding: 0.8em 1.2em;
    font-size: unquote("min(max( 1.5em ,2vw),2em)");
    animation: fade_in 1.2s ease-in-out 0.1s both;
    justify-content: center;

    &.have_input{
        min-width: 15em;
        height: 1em;

        & input{
            cursor: text;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            padding: 0;
            outline: none;
            background-color: transparent;
            border: none;
            font-size: 1.8em;
            text-align: center;
            &.inline{
                width: 80%;
                &+button{
                    position: absolute;
                    top: 0;
                    right: 0;
                    height: 100%;
                    width: 20%;
                    font-size: 1em;
                    border-radius: 10em;
                    border-width: 0;
                    background:rgba(#fff,.4);
                    box-shadow: 0 0 0.1em 0 rgba(0, 0, 0, 0.4);
                }
            }
        }
    }
}
@keyframes fade_in {
    from {
        opacity: 0;
        transform: translateY(100%);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
